import { h, render, Component } from 'preact';
import { getLink } from '@/utils/instruments';
import { themeable } from '@/utils';

import I18n from '@/app/i18n';

import styles from './style.css';

export class SymbolRow extends Component {
    shouldComponentUpdate (nextProps) {
        this.tickClass =
            this.props.value === nextProps.value
                ? ''
                : this.props.value > nextProps.value
                    ? this.props.theme['tickNeg']
                    : this.props.theme['tickPos'];

        return (
            this.props.value !== nextProps.value ||
            this.props.width !== nextProps.width
        );
    }

    componentWillUpdate () {
        if (this.tickClass.length && this.tickerContainer) {
            window.requestAnimationFrame((t) => {
                let tickClass = this.tickClass
                this.tickerContainer.classList.remove(this.props.theme['tickPos']);
                this.tickerContainer.classList.remove(this.props.theme['tickNeg']);
                window.requestAnimationFrame((t) => {
                    if(this.tickerContainer && tickClass) this.tickerContainer.classList.add(tickClass);
                });
            });
        }
    }

    render ({
        alias,
        value,
        symbol,
        type,
        market,
        percentChange,
        width,
        className,
        theme
    }) {

        let rowrClassName = [styles.row, theme.tableRow];

        if (width >= 480) {
            rowrClassName.push(styles.width480);
        }

        if (width >= 385) {
            rowrClassName.push(styles.width375);
        }

        if (className) {
            rowrClassName.push(className);
        }

        return (
            <div className={rowrClassName.join(' ')}>
                <div className={styles.name}>{alias}</div>
                <div className={styles.values}>
                    <div
                        className={styles.value}
                        ref={elm => {
                            this.tickerContainer = elm;
                        }}
                    >
                        {value}
                    </div>
                    <div
                        className={
                            percentChange > 0
                                ? `${styles.precent} ${theme.colorPositive}`
                                : `${styles.precent} ${theme.colorNegative}`
                        }
                    >
                        {percentChange}%
                    </div>
                </div>
                <div className={styles.buttons}>
                    <a
                        href={getLink({ market, type, symbol })}
                        target="_blank"
                        className={`${styles.trade} ${theme.btnPrimary}`}>{I18n.t("Trade")}</a>
                    <a
                        href={getLink({ market, type, symbol })}
                        target="_blank"
                        className={theme.btnUp}>{I18n.t("Buys")}</a>
                    <a
                        href={getLink({ market, type, symbol })}
                        target="_blank"
                        className={theme.btnDown}>{I18n.t("Sell")}</a>
                </div>
            </div>
        );
    }
}

export default themeable(SymbolRow);



