import extend from 'extend';
import I18n from "i18n-js";
import i18nline from "i18nline/lib/extensions/i18n_js";
import preactI18nline from "preact-i18nline/dist/extensions/i18n_js";

i18nline(I18n);
preactI18nline(I18n);

let translationFiles = require.context("../i18n", true, /\.json$/);
translationFiles.keys().forEach(function(key) {
  extend(true, I18n, {translations: translationFiles(key)});
});

export { I18n as default}


